@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Roboto:wght@300&display=swap');


.residence {
    width: 100%;
    height: 100vh;
    background: rgba(17, 17, 17, 0.219);
    position: relative;
  }
  
  .residence:before {
    content: '';
    position: absolute;
    background: url('../../assets/submenu-bg-residence.jpg') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    z-index: -1;
  }
  


.residence .residence-content {
    display: block;
    position: absolute;
    top: 30%;
    right: 10%;
    padding: 1rem;
}

.residence h1 {
    font-size: 4rem;
    font-family: 'Montserrat', sans-serif;
    color: white;
}

/* MEDIA QUERIES */

@media screen and (max-width:940px) {

.residence h1 {
    font-size: 3rem;
    text-align: center;
}

.residence .residence-content {
    width: 100%;
    top: 60%;
    right: 0%;
}

}